<template>
  <v-navigation-drawer v-model="drawer" app>
    <v-list-item>
      <v-list-item-action>
        <v-list-item-avatar>
          <img
            src="https://d.ifengimg.com/w480_h270_q90/x0.ifengimg.com/ucms/2021_20/10A7E0895D3E36982D255A8F3FE57D022FC00CD9_size44_w640_h360.jpg"
          />
        </v-list-item-avatar>
      </v-list-item-action>
      <v-list-item-content>
        <v-list-item-title class="title"> Application </v-list-item-title>
        <v-list-item-subtitle> subtext </v-list-item-subtitle>
      </v-list-item-content>
    </v-list-item>

    <v-divider></v-divider>

    <v-list dense nav>
      <template v-for="(item, i) in items">
        <v-divider v-if="item.divider" :key="i" class="my-4"> </v-divider>

        <v-list-item v-else-if="!item.children" :key="i" link :to="item.link">
          <v-list-item-icon>
            <v-icon :color="item.color ? item.color : ''">{{
              item.icon
            }}</v-icon>
          </v-list-item-icon>
          <v-list-item-content>
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>

        <v-list-group v-else :key="i" :prepend-icon="item.icon" no-action>
          <template v-slot:activator>
            <v-list-item-content>
              <v-list-item-title> {{ item.title }}</v-list-item-title>
            </v-list-item-content>
          </template>
          <v-list-item
            v-for="(child, i) in item.children"
            :key="i"
            link
            :to="child.link"
          >
            <v-list-item-content>
              <v-list-item-title> {{ child.title }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list-group>
      </template>

      <!-- prepend-icon 前面的图标 -->
      <!-- no-action 子项缩进 -->
      <v-list-group prepend-icon="mdi-book-multiple-outline" no-action>
        <!-- template 展开或收缩子项点击的菜单项 -->
        <template v-slot:activator>
          <v-list-item-content>
            <v-list-item-title> Books </v-list-item-title>
          </v-list-item-content>
        </template>
        <!--  </template> 与</v-list-group> 之间放用于展开的子项 -->
        <v-list-item link>
          <v-list-item-content>
            <v-list-item-title>Book list</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-list-item link>
          <v-list-item-content>
            <v-list-item-title>Book list</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-list-item link>
          <v-list-item-content>
            <v-list-item-title>Book list</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list-group>
    </v-list>
  </v-navigation-drawer>
</template>

