        },
        {
          title: "计数器",
          icon: "mdi-image",
          chip: 10,
          color: "success",
          link: "/dashboard/counter",
        },
        {
          title: "New Topic",
          icon: "mdi-square-edit-outline",
          chip: 10,
          color: "primary",
          link: "/dashboard/createtopic",
        },
        // { title: "Tables", icon: "mdi-grid-large", link: "/tables" },
        // {
        //   title: "Notifications",
        //   icon: "mdi-bell-outline",
        //   link: "/notifications",
        // },
        {
          title: "Forum Manager",
          icon: "mdi-sprout-outline",
          active: false,
          children: [
            {
              title: "Category Manager",
              icon: "mdi-circle-small",
              chip: 6,
              link:'/dashboard/categorymanager',
            },
             {
              title: "Topic Manager",
              icon: "mdi-circle-small",
              chip: 6,
              link:'/dashboard/topicmanager',
            },
            {
              title: "分类",
              icon: "mdi-bell-outline",
              link: "/dashboard/createcategory",
            },
            {
              title: "商品属性",
              icon: "mdi-circle-small",
            },
          ],
        },
        // { divider: true },
        // { heading: "HELP" },
        {
          title: "Library",
          icon: "mdi-book-variant-multiple",
          active: false,
          children: [
            {
              title: "All Books",
              icon: "mdi-book-multiple-outline",
              link: "",
            },
            {
              title: "Books Category",
              icon: "mdi-book-check-outline",
              link: "",
            },
            {
              title: "Book Property",
              icon: "mdi-book-open-blank-variant",
